html{
    width: 100%;
    height: 100%;
}
body {
    background-color: #18181b !important;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.content{
    margin-top: 100px;
}

.text{
    text-align: center;
    font-family: Roobert,Inter,Helvetica Neue,Helvetica,Arial,sans-seri,
    monospace;
}

.name{
    font-size: 50px; 
    transition: all 300ms ease-out;
    color: #9147ff;
    position: relative;
}

.name:hover{
    color: #772ce8; 
    transition: all 300ms ease-out;
}


p.text{
    font-size: 25px;
    color: #ffffff;
    transition: all 300ms ease-out;
}

p.text:hover{
    color: #9147ff;
}

.info {
    padding-left: 20px;
}

.presentation{
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.secondary-purple{
    color:#772ce8;
}

.paragraph {
    color: white;

    text-align:center;
    margin-left: 100px;
    margin-right: 100px;
    display: inline-block;
    font-family: Roobert,Inter,Helvetica Neue,Helvetica,Arial,sans-seri,
    monospace;
}
 
.last-paragraph{
    margin-bottom: 350px;
}

.infos{
    margin-top: 350px;
}

@media only screen and (max-width: 500px) {
    .info {
        padding-left: 0;
    }
}

@media only screen and (max-width: 580px) {

    .name {
        font-size: 40px;
    }

    p.text{
        font-size: 20px;
    }

    .paragraph {
        margin-left: 15px;
        margin-right: 15px;
    }
}

@media only screen and (max-width: 380px) {

    .name {
        font-size: 30px;
    }

    p.text{
        font-size: 15px;
    }

    .content{
        margin-top: 50px;
    }
}